import { dataConst } from './const';

export default (editor, opts = {}) => {
    const {
        id, label, block,
        labelDetail, idDetail, blockDetail
    } = opts;

    editor.BlockManager.add(id, {
        label: label,
        content: { type: id },
        media: `<i class="fa-brands fa-product-hunt fa-3x"></i>`,
        category: 'Data Vision',
        select: true,
        ...block,
    });

    editor.BlockManager.add(idDetail, {
        label: labelDetail,
        content: { type: idDetail },
        media: `<i class="fa-solid fa-cube fa-3x"></i>`,
        category: 'Data Vision',
        select: true,
        ...blockDetail,
    })
}
