import loadProductImageSlider from './slider';

export default (editor, opts={}) => {
    const {
        idDetail, labelDetail,
        idColPicture, labelColPicture, 
        internalOpts,
    } = opts;

    // load Slider For
    const [idSliderFor, labelSlideFor] = [`${idDetail}-slider-for`, `${labelDetail} Slider For`];
    const [idSliderItemFor, labelSliderItemFor] = [`${idDetail}-slider-for-item`, `${labelDetail} Slider For Item`];
    loadProductImageSlider(editor, {
        ...opts,
        prefixClass: 'slider-for',
        extClass: 'slider mb-3',
        internalOpts: internalOpts,
        idSlider: idSliderFor, 
        labelSlide: labelSlideFor,
        idSliderItem: idSliderItemFor, 
        labelSliderItem: labelSliderItemFor,
    });

    // load Slider Nav
    const [idSliderNav, labelSlideNav] = [`${idDetail}-slider-nav`, `${labelDetail} Slider Nav`];
    const [idSliderItemNav, labelSliderItemNav] = [`${idDetail}-slider-nav-item`, `${labelDetail} Slider Nav Item`];
    loadProductImageSlider(editor, {
        ...opts,
        prefixClass: 'slider-nav',
        extClass: 'slider',
        internalOpts: internalOpts,
        idSlider: idSliderNav, 
        labelSlide: labelSlideNav,
        idSliderItem: idSliderItemNav, 
        labelSliderItem: labelSliderItemNav,
    });

    editor.DomComponents.addType(idColPicture, {
        model: {
            defaults: {
                ...internalOpts,
                name: labelColPicture,
                attributes: {
                    class: `positon-relative col-12 col-lg-4 p-3`,
                },
                styles: `
                    .slider {width: 80%;margin: 0 auto;}
                    .slick-slide img {width: 100%;max-width: 250px;max-height: 250px;}               
                    .slick-prev::before {color: #4f4f4f!important;}
                    .slick-next::before {color: #4f4f4f!important;}
                    .slider-for-item.slick-slide {margin: 0px 20px;}          
                    .slider-nav-item.slick-slide {margin: 0px 5px;}
                    .slider-nav-item.slick-slide img {width: 100%;max-height: 60px;margin-right: 10px;}
                `,
                components: [
                    {
                        tagName: 'div',
                        attributes: {
                            class: 'w-100 h-100',
                            "data-x-cls": "my-product-detail---picture",
                        },
                        components: [
                            {
                                tagName: 'div',
                                attributes: {
                                    id: 'my-product-image--zoom',
                                    class: 'img-zoom-result position-absolute top-0 d-none',
                                },
                                styles: `
                                    #my-product-image--zoom {
                                        right: 0; z-index: 9999;
                                    }
                                    .img-zoom-lens {
                                        position: absolute;
                                        border: 1px solid #d4d4d4;
                                        width: 80px;
                                        height: 80px;
                                        cursor: zoom-in;
                                    }
                                            
                                    .img-zoom-result {
                                        border: 1px solid #d4d4d4;
                                        width: 300px;
                                        height: 300px;
                                    }
                                `,
                            },
                            {
                                type: idSliderFor,
                                components: [
                                    { type: idSliderItemFor },
                                ]
                            },
                            {
                                type: idSliderNav,
                                components: [
                                    { type: idSliderItemNav },
                                ],
                            },
                        ]
                    }
                ],
                script: function () {
                    function imageZoom(img, result) {
                        /*create lens:*/
                        let lens = img.parent().find('.img-zoom-lens');
                        if (lens.length === 0) {
                            lens = $("<div class='img-zoom-lens' style='display: none;'></div>");
                            img.parent().append(lens);
                        }

                        /*calculate the ratio between result DIV and lens:*/
                        let cx = result.width() / lens.width();
                        let cy = result.height() / lens.height();

                        /*set background properties for the result DIV:*/
                        result.css({
                            "background-image": "url('" + img.attr("src") + "')",
                            "background-size": (img.width() * cx) + "px " + (img.height() * cy) + "px",
                            "left": result.parent().outerWidth() + 20 + 'px',
                        });

                        /*execute a function when someone moves the cursor over the image, or the lens:*/
                        lens.on("mousemove", moveLens);
                        img.on("mousemove", moveLens);

                        /*and also for touch screens:*/
                        lens.on("touchmove", moveLens);
                        img.on("touchmove", moveLens);

                        /* show result when move inside image | hide result when move outside image */
                        lens.on("mouseenter", (e) => {
                            lens.show();
                            result.removeClass('d-none');
                        }); // Show result on mouse enter
                        lens.on("mouseleave", (e) => {
                            lens.hide();
                            result.addClass('d-none');
                        }); // Hide result on mouse leave

                        /* Function utils */
                        function moveLens(e) {
                            lens.show();

                            var pos, x, y;
                            /*prevent any other actions that may occur when moving over the image:*/
                            e.preventDefault();
                            /*get the cursor's x and y positions:*/
                            pos = getCursorPos(e);
                            /*calculate the position of the lens:*/
                            x = pos.x - (lens.width() / 2);
                            y = pos.y - (lens.height() / 2);
                            /*prevent the lens from being positioned outside the image:*/
                            if (x > img.width() - lens.width()) { x = img.width() - lens.width(); }
                            if (x < 0) { x = 0; }
                            if (y > img.height() - lens.height()) { y = img.height() - lens.height(); }
                            if (y < 0) { y = 0; }
                            /*set the position of the lens:*/
                            lens.css({
                                "left": x + "px",
                                "top": y + "px"
                            });
                            /*display what the lens "sees":*/
                            result.css({
                                "background-position": "-" + (x * cx) + "px -" + (y * cy) + "px"
                            });
                        }

                        function getCursorPos(e) {
                            var a, x = 0, y = 0;
                            e = e || window.event;
                            /*get the x and y positions of the image:*/
                            a = img[0].getBoundingClientRect();
                            /*calculate the cursor's x and y coordinates, relative to the image:*/
                            x = e.pageX - a.left;
                            y = e.pageY - a.top;
                            /*consider any page scrolling:*/
                            x = x - window.pageXOffset;
                            y = y - window.pageYOffset;
                            return { x: x, y: y };
                        }
                    }

                    let sliderFor = $(this).find('.slider-for');
                    let sliderNav = $(this).find('.slider-nav');

                    sliderFor.slick({
                        slidesToShow: 1,
                        slidesToScroll: 1,
                        arrows: false,
                        fade: true,
                        accessibility: false,
                        speed: 0,
                    });
                    sliderNav.slick({
                        slidesToShow: 5,
                        slidesToScroll: 1,
                        dots: true,
                        centerMode: false,
                        focusOnSelect: false,
                        infinite: false,
                        speed: 0,
                        variableWidth: false,
                    });

                    sliderNav.find('.slider-nav-item.slick-slide').first().find('img').addClass("border border-warning");
                    imageZoom(sliderFor.find('.slider-for-item.slick-slide.slick-current.slick-active > img'), $(`#my-product-image--zoom`));

                    $(this).find('.slider-nav .slider-nav-item.slick-slide').on('mouseenter', function (event) {
                        // active slide Thumnails
                        sliderFor.slick('slickGoTo', $(this).data('slickIndex'));

                        // border select Navigation
                        sliderNav.find('.slider-nav-item.slick-slide img').removeClass("border border-danger");
                        $(this).find('img').addClass("border border-warning");

                        imageZoom(sliderFor.find('.slider-for-item.slick-slide.slick-current.slick-active > img'), $(`#my-product-image--zoom`));
                    });
                },
            }
        }
    })
}