import loadProductDetail from './components/product-detail/index';
import loadProductList from './components/product-list/index';

export default (editor, opts = {}) => {
    const { id, label, productData } = opts;

    //  --------------------------
    //  Table Product
    //  --------------------------

    loadProductList(editor, {
        ...opts,
        id: id,
        label: label,
        productData: productData,
    })


    //  --------------------------
    //  Product Detail
    //  --------------------------

    const { idDetail, labelDetail, } = opts;
    const [idDetailItem, labelDetailItem] = [`${idDetail}-row`, `${labelDetail} Row`];

    const internalOpts = {
        tagName: 'div',
        dropable: false,
        dragable: false,
    }

    loadProductDetail(editor, {
        ...opts,
        idDetail: idDetail,
        labelDetail: labelDetail,
        internalOpts: internalOpts,
        idDetailItem: idDetailItem,
        labelDetailItem: labelDetailItem,
    })

    editor.DomComponents.addType(idDetail, {
        model: {
            defaults: {
                ...internalOpts,

                // editable: false,
                // removeable: false,
                // locked: true,
                // selectable: false,
                // hoverable: false,

                name: labelDetail,
                tagName: 'div',
                attributes: {
                    class: 'container py-3 bg-white',
                    "data-x-cls": "my-products-detail",
                },
                components: [
                    {type: idDetailItem},
                ],
            }
        }
    })
};
