export default (editor, opts = {}) => {
    const { internalOpts, idRowRemark, labelRowRemark } = opts;

    editor.DomComponents.addType(idRowRemark, {
        model: {
            defaults: {
                ...internalOpts,
                name: labelRowRemark,
                tagName: 'div',
                attributes: {
                    class: "w-100",
                },
                components: [
                    {
                        tagName: 'div',
                        attributes: {
                            class: "bg-light w-100 p-3",
                        },
                        components: {
                            tagName: 'p',
                            attributes: {
                                class: "p-0 m-0 text-muted",
                            },
                            components: editor.I18n.t('grapesjs-plugin-my-products.descriptionProduct'),
                        }
                    },
                    {
                        tagName: 'div',
                        attributes: {
                            class: "border border-1 border-light h-300p p-3 text-wrap",
                            "data-x-cls": "my-product-detail-remark",
                        },
                        components: ``,
                    },
                ]
            }
        }
    })
}