import loadColImage from './col-img';
import loadColInfo from './col-info';
import loadColDelivery from './col-delivery';
import loadDetailRemark from './col-detail-remark';

export default (editor, opts = {}) => {
    const { 
        idDetail, labelDetail, internalOpts, idDetailItem, labelDetailItem,
        productData,
    } = opts;

    // load Col Image
    const [idColPicture, labelColPicture] = [`${idDetailItem}-col-picture`, `${labelDetail} Col Picture`];
    loadColImage(editor, {
        ...opts,
        internalOpts: internalOpts,
        idDetail: idDetail, labelDetail: labelDetail,
        idColPicture: idColPicture,
        labelColPicture: labelColPicture,
    })

    // load Col Info
    const [idColInfo, labelColInfo] = [`${idDetailItem}-col-info`, `${labelDetail} Col Info`];
    loadColInfo(editor, {
        ...opts,
        internalOpts: internalOpts,
        idColInfo: idColInfo,
        labelColInfo: labelColInfo
    })

    // load Col Delivery
    const [idColDelivery, labelColDelivery] = [`${idDetailItem}-col-delivery`, `${labelDetail} Col Delivery`];
    loadColDelivery(editor, {
        ...opts,
        internalOpts: internalOpts,
        idColDelivery: idColDelivery,
        labelColDelivery: labelColDelivery,
    })

    // load Row remark
    const [idRowRemark, labelRowRemark] = [`${idDetailItem}-remark`, `${labelDetail} Remark`];
    loadDetailRemark(editor, {
        ...opts,
        internalOpts: internalOpts,
        idRowRemark: idRowRemark,
        labelRowRemark: labelRowRemark,
    })

    // main
    editor.DomComponents.addType(idDetailItem, {
        model: {
            defaults: {
                ...internalOpts,
                name: labelDetailItem,
                components: [
                    {
                        ...internalOpts,
                        name: `${labelDetail} Row Main Info`,
                        attributes: {
                            class: "row w-100 p-0 border border-light",
                            "data-x-cls": "my-product-detail-all",
                            "data-base-detail-url": productData.linkDetail,
                            "data-base-detail-api-url": productData.linkAPIDetail,
                        },
                        components: [
                            {type: idColPicture},
                            {type: idColInfo},
                            {type: idColDelivery},
                        ],
                    },
                    {
                        ...internalOpts,
                        name: `${labelDetail} Row Remarks`,
                        attributes: {
                            class: "row w-100 p-0 mt-1",
                        },
                        components: {
                            type: idRowRemark,
                        },
                    }
                ],
                script: function(){
                    let clsThis = this;

                    function checkUUID4(data){
                        if (
                            typeof data === "string" && (
                                data.length === 36 // string type
                                || data.length === 36 - 4 // hex type
                            )
                        ) {
                            const regexExp = /^[0-9a-fA-F]{8}\b-[0-9a-fA-F]{4}\b-[0-9a-fA-F]{4}\b-[0-9a-fA-F]{4}\b-[0-9a-fA-F]{12}$/gi;
                            return regexExp.test(data);
                        }
                        return false;
                    }

                    function convertThousand(value, currentCode = '') {
                        let arr = [];
                        if (value) {
                            let arrData = value.toString().split("").reverse();
                            arrData.map(
                                (item, idx) => {
                                    if (idx !== 0 && idx % 3 === 0 && idx !== arrData.length - 1) {
                                        arr.push(".");
                                        arr.push(item);
                                    } else {
                                        arr.push(item);
                                    }
                                }
                            )
                        }
                        return `${arr.reverse().join("")} ${currentCode.toUpperCase()}`;
                    }

                    function imageZoom(img, result) {
                        /*create lens:*/
                        let lens = img.parent().find('.img-zoom-lens');
                        if (lens.length === 0) {
                            lens = $("<div class='img-zoom-lens' style='display: none;'></div>");
                            img.parent().append(lens);
                        }

                        /*calculate the ratio between result DIV and lens:*/
                        let cx = result.width() / lens.width();
                        let cy = result.height() / lens.height();

                        /*set background properties for the result DIV:*/
                        let cssSetter = {
                            "background-image": `url(${img.attr("src")})`,
                            "background-size": (img.width() * cx) + "px " + (img.height() * cy) + "px",
                            "left": result.parent().outerWidth() + 20 + 'px',
                        }
                        result.css(cssSetter);

                        /*execute a function when someone moves the cursor over the image, or the lens:*/
                        lens.on("mousemove", moveLens);
                        img.on("mousemove", moveLens);

                        /*and also for touch screens:*/
                        lens.on("touchmove", moveLens);
                        img.on("touchmove", moveLens);

                        /* show result when move inside image | hide result when move outside image */
                        lens.on("mouseenter", (e) => {
                            lens.show();
                            result.removeClass('d-none');
                        }); // Show result on mouse enter
                        lens.on("mouseleave", (e) => {
                            lens.hide();
                            result.addClass('d-none');
                        }); // Hide result on mouse leave

                        /* Function utils */
                        function moveLens(e) {
                            lens.show();

                            var pos, x, y;
                            /*prevent any other actions that may occur when moving over the image:*/
                            e.preventDefault();
                            /*get the cursor's x and y positions:*/
                            pos = getCursorPos(e);
                            /*calculate the position of the lens:*/
                            x = pos.x - (lens.width() / 2);
                            y = pos.y - (lens.height() / 2);
                            /*prevent the lens from being positioned outside the image:*/
                            if (x > img.width() - lens.width()) { x = img.width() - lens.width(); }
                            if (x < 0) { x = 0; }
                            if (y > img.height() - lens.height()) { y = img.height() - lens.height(); }
                            if (y < 0) { y = 0; }
                            /*set the position of the lens:*/
                            lens.css({
                                "left": x + "px",
                                "top": y + "px"
                            });
                            /*display what the lens "sees":*/
                            result.css({
                                "background-position": "-" + (x * cx) + "px -" + (y * cy) + "px"
                            });
                        }

                        function getCursorPos(e) {
                            var a, x = 0, y = 0;
                            e = e || window.event;
                            /*get the x and y positions of the image:*/
                            a = img[0].getBoundingClientRect();
                            /*calculate the cursor's x and y coordinates, relative to the image:*/
                            x = e.pageX - a.left;
                            y = e.pageY - a.top;
                            /*consider any page scrolling:*/
                            x = x - window.pageXOffset;
                            y = y - window.pageYOffset;
                            return { x: x, y: y };
                        }
                    }

                    class loadProductDetail{
                        constructor (ele, data){
                            this.ele = ele;
                            this.data = data;
                        }

                        load(){
                            this.loadBanner();
                            this.loadPrice();
                            this.loadFavour();
                            this.loadVariant();
                            this.loadRemark();
                            this.loadWarranty();
                            this.loadPicture();
                        }

                        loadBanner(){
                            $(this.ele).find('[data-x-cls="my-product-detail--banner"]').addClass('d-none');
                        }

                        loadPrice(){
                            $(this.ele).find('[data-x-cls="my-product-detail--title"]').text(this.data?.title || '');
                            $(this.ele).find('[data-x-cls="my-product-detail--price"]').text(
                                convertThousand(this.data?.sale_price || 0)
                            );
                            $(this.ele).find('[data-x-cls="my-product-detail--price--off"]').addClass('d-none');
                        }

                        loadFavour(){
                            $(this.ele).find('[data-x-cls="my-product-detail--favour"]').addClass('d-none');
                        }

                        loadVariant(){
                            $(this.ele).find('[data-x-cls="my-product-detail--variant"]').addClass('d-none');
                        }

                        loadRemark(){
                            $(this.ele).find('[data-x-cls="my-product-detail-remark"]').html(this.data?.description || '');
                        }

                        loadWarranty(){
                            $(this.ele).find('[data-x-cls="my-product-detail--warranty"]').addClass("d-none").prev('hr').addClass('d-none');
                        }

                        loadPicture(){
                            let picEle = $(this.ele).find('[data-x-cls="my-product-detail---picture"]');
                            if (picEle.length > 0){
                                picEle.hide();

                                let forEle = $(picEle).find('[data-x-cls="slider-for"]');
                                let navEle = $(picEle).find('[data-x-cls="slider-nav"]');

                                // destroy -> generate from first element -> init slick()
                                forEle.slick('destroy');
                                navEle.slick('destroy');

                                // remove exclude first item
                                let forFirstEle = forEle.find('[data-x-cls="slider-for-item"]').first();
                                forEle.find('[data-x-cls="slider-for-item"]:not(:first)').remove();
                                let navFirstEle = navEle.find('[data-x-cls="slider-nav-item"]').first().removeClass("border border-warning");
                                navEle.find('[data-x-cls="slider-nav-item"]:not(:first)').remove();
                                
                                // element zoom
                                let zoomEle = $(`#my-product-image--zoom`);

                                // check response data && generate
                                let avatarData = this.data.avatar || null;
                                if (avatarData){
                                    let forClone = forFirstEle.clone();
                                    forClone.find('img').attr('src', avatarData);
                                    forEle.append(forClone);
                                    forFirstEle.remove();
                                    forEle.slick({
                                        slidesToShow: 1,
                                        slidesToScroll: 1,
                                        arrows: false,
                                        fade: true,
                                        accessibility: false,
                                        speed: 0,
                                    });

                                    let navClone = navFirstEle.clone();
                                    navClone.find('img').attr('src', avatarData);
                                    navEle.append(navClone);
                                    navFirstEle.remove();
                                    navEle.slick({
                                        slidesToShow: 5,
                                        slidesToScroll: 1,
                                        dots: true,
                                        centerMode: false,
                                        focusOnSelect: false,
                                        infinite: false,
                                        speed: 0,
                                        variableWidth: false,
                                    });

                                    picEle.show({
                                        complete: function(){
                                            imageZoom(forClone.find('img'), zoomEle);
                                        }
                                    });
                                    
                                } 
                            }
                            
                        }
                    }

                    let mainEle = $(this).find(`[data-x-cls="my-product-detail-all"]`);
                    if (mainEle.length > 0){
                        let baseDetailUrl = mainEle.attr('data-base-detail-url');
                        let baseAPIUrl = mainEle.attr('data-base-detail-api-url');

                        let currentPath = baseDetailUrl.startsWith('/test/product/') ? '/test/product/3a82c590-cb82-4f7c-aba6-cfcb383af8e2' : window.location.pathname;

                        let baseUrlReg = baseDetailUrl.replace("{id}", "(?<id>[0-9a-f]{8}-[0-9a-f]{4}-[0-9a-f]{4}-[0-9a-f]{4}-[0-9a-f]{12})");

                        let reg = new RegExp('(.)*' + baseUrlReg, 'gm');
                        let match = reg.exec(currentPath);
                        if (match){
                            const idx = match.groups.id;
                            if (checkUUID4(idx)){
                                let apiUrl = baseAPIUrl.replaceAll("{id}", idx);

                                if (apiUrl.startsWith("/test/api/product/")){
                                    // fake data
                                    console.log(
                                        '%c Response My-Product-Detail API return format:',
                                        'background: #222; color: red',
                                        {
                                            "data": {
                                                "id": "{id}",
                                                "title": "{name}",
                                                "available_amount": 40,
                                                "avatar": "{link}",
                                                "description": "{remark}",
                                                "sale_price": 1000,
                                            }
                                        }
                                    )
                                    $.mockjax({
                                        url: apiUrl,
                                        type: 'GET',
                                        contentType: "application/json",
                                        responseText: {
                                            "data": {
                                                "id": "3a82c590-cb82-4f7c-aba6-cfcb383af8e2",
                                                "title": "My in HP",
                                                "code": "PRD.0005",
                                                "stock_amount": 123,
                                                "avatar": "https://www.volusion.com/blog/content/images/2021/07/Product-Photos.jpg",
                                                "description": "My in mu",
                                                "sale_price": 15000000
                                            }
                                        },
                                    });
                                }

                                if (!$.fn.callAjaxPublic) {
                                    $.fn.callAjaxPublic = function (config) {
                                        return new Promise(
                                            (resolve, reject) => {
                                                let { url, method } = config;
                                                let ctx = {
                                                    url: url,
                                                    type: method, // dataType: 'json',
                                                    contentType: "application/json",
                                                    processData: true,
                                                    data: 'GET',
                                                    headers: {
                                                    },
                                                    success: function (rest, textStatus, jqXHR) {
                                                        resolve(rest);
                                                    },
                                                    error: function (jqXHR, textStatus, errorThrown) {
                                                        let resp_data = jqXHR.responseJSON;
                                                        if (resp_data && typeof resp_data === 'object') {
                                                            reject(resp_data);
                                                        } else if (jqXHR.status === 204) reject({ 'status': 204 });
                                                    },
                                                }
                                                return $.ajax(ctx);
                                            }
                                        )
                                    }
                                }

                                $.fn.callAjaxPublic({
                                    url: apiUrl,
                                    type: 'GET',
                                }).then(
                                    (resp) => {
                                        let data = resp?.['data'] || null;
                                        if (data && typeof data === 'object'){
                                            new loadProductDetail(clsThis, data).load();
                                        }
                                    },
                                    (errs) => {
                                        console.log('errs:', errs);
                                    }
                                )
                            }
                        }
                    }
                },
            }
        }
    })
}