import { dataConst } from "../../const";

export default (editor, opts = {}) => {
    const { label, idTableData, idTableDataItem, idTableDataItemLoad, productData } = opts;

    editor.DomComponents.addType(idTableDataItemLoad, {
        model: {
            defaults: {
                name: `${label} Data Item Load`,
                dropable: false,
                dragable: false,
                tagName: 'div',
                attributes: {
                    class: 'row p-0 m-0 w-100 overflow-hidden',
                },
                components: [
                    {
                        tagName: 'div',
                        dragable: false,
                        dropable: false,
                        editable: false,
                        attributes: {
                            class: 'col-12 w-100 p-0 d-flex justify-content-center align-items-center',
                            "data-x-cls": "my-product-img",
                        },
                        components: [
                            {
                                type: 'image',
                                dragable: false,
                                dropable: false,
                                editable: false,
                                attributes: {
                                    class: "w-100 h-100",
                                    src: dataConst.productImg,
                                    style: "max-height: 200px!important; object-fit: contain;",
                                    "data-x-cls": "my-product-img--img",
                                },
                            },
                        ],
                        isComponent: el => {
                            return el.getAttribute('data-x-cls') === 'my-product-img';
                        },
                    },
                    {
                        tagName: 'div',
                        dragable: false,
                        dropable: false,
                        editable: false,
                        attributes: {
                            class: "col-12 w-100 p-0",
                            "data-x-cls": "my-product-info",
                        },
                        script: function () {
                            $(document).find('div[data-x-cls="my-product-info"]').each(function () {
                                if ($(this).hasClass('col-12')) {
                                    $(this).find('[data-x-cls="title-line-text"],[data-x-cls="remark-line-text"]').each(function () {
                                        if (!$(this).attr('title')) {
                                            let currentText = $(this).text();
                                            $(this).attr('title', currentText);

                                            let smallTxt = '';
                                            if (currentText.length > 100) {
                                                smallTxt = `${currentText.slice(0, 100)}...`;
                                            } else {
                                                smallTxt = `${currentText}${" ".repeat(100 - currentTextlengt)}`;
                                            }
                                            $(this).attr('data-title-sm', smallTxt);
                                            $(this).text(smallTxt);
                                        }

                                    });
                                }
                            })
                        },
                        components: [
                            {
                                type: 'text',
                                tagName: 'h6',
                                dragable: false,
                                dropable: false,
                                editable: false,
                                attributes: {
                                    class: "text-wrap",
                                    "data-x-cls": "title-line-text",
                                    "data-toggle": "tooltip",
                                    "data-placement": "bottom",
                                    "title": "",
                                },
                                components: 'Title of product. '.repeat(20),
                            },
                            {
                                type: 'text',
                                tagName: 'p',
                                dragable: false,
                                dropable: false,
                                editable: false,
                                attributes: {
                                    "data-x-cls": "price-line-text",
                                },
                                components: '12.500.000 VND',
                            },
                            {
                                type: 'text',
                                tagName: 'small',
                                dragable: false,
                                dropable: false,
                                editable: false,
                                attributes: {
                                    class: "w-100 d-inline-block",
                                    "data-x-cls": "remark-line-text",
                                    "data-toggle": "tooltip",
                                    "data-placement": "bottom",
                                    "title": "",
                                },
                                components: 'Remarks '.repeat(50),
                            },
                            {
                                type: 'text',
                                tagName: 'p',
                                dragable: false,
                                dropable: false,
                                editable: false,
                                removeable: false,
                                attributes: {
                                    
                                    "data-x-cls": "my-product-data--amout-all",
                                },
                                components: [
                                    editor.I18n.t('grapesjs-plugin-my-products.amountLeftBefore'),
                                    {
                                        type: 'text',
                                        tagName: 'span',
                                        dragable: false,
                                        dropable: false,
                                        editable: false,
                                        removeable: false,
                                        attributes: {
                                            class: "mx-1",
                                            "data-x-cls": "my-product-data--amout-left",
                                        },
                                        components: `0`
                                    },
                                    editor.I18n.t('grapesjs-plugin-my-products.amountLeftAfter'),
                                ],
                            },
                        ],
                        isComponent: el => {
                            return el.getAttribute('data-x-cls') === 'my-product-info';
                        },
                    },

                ],
            },
        }
    })

    editor.DomComponents.addType(idTableDataItem, {
        model: {
            defaults: {
                name: `${label} Data Item`,
                dropable: false,
                dragable: false,
                tagName: 'div',
                attributes: {
                    class: 'col-12 col-sm-6 col-md-4 col-lg-3 p-3',
                    "data-x-cls": "my-product-item",
                    "data-is-pattern": "true",
                },
                styles: `
                    div[data-x-cls="my-product-item"]:hover {
                        background-color: #f0f0f1;
                    }
                `,
                components: [
                    {
                        type: 'link',
                        tagName: 'a',
                        attributes: {
                            href: productData.linkDetail || '#',
                            "data-x-cls": "my-product-item--link",
                        },
                        style: {
                            color: 'inherit',
                            "text-decoration": "inherit",
                        },
                        components: { type: idTableDataItemLoad },
                    }
                ],
                isComponent: el => {
                    return el.getAttribute('data-x-cls') === 'my-product-item';
                },
            }
        }
    })

    editor.DomComponents.addType(idTableData, {
        model: {
            defaults: {
                name: `${label} Table Data`,
                dropable: false,
                dragable: false,
                tagName: 'div',
                attributes: {
                    class: 'row',
                    "data-x-cls": "my-product-data",
                },
                components: [
                ],
            },
        }
    })
}