import loadProducts from './products';
import loadToolbar from './toolbar';

export default (editor, opts = {}) => {
    const { id, label, productData } = opts;

    // Toolbar
    const idToolbarHead = `${id}-toolbar-head`;
    const idToolbarMatchQueryCount = `${id}-toolbar-query-count`;
    const idPageButton = `${id}-toolbar-foot-page-button`;
    const idToolbarFoot = `${id}-toolbar-foot`;
    loadToolbar(editor, {
        ...opts,
        idToolbarHead: idToolbarHead,
        idPageButton: idPageButton,
        idToolbarFoot: idToolbarFoot,
        idToolbarMatchQueryCount: idToolbarMatchQueryCount,
    })

    // Table Data
    const idTableData = `${id}-table-data`;
    const idTableDataItem = `${id}-table-data-item`;
    const idTableDataItemLoad = `${id}-table-data-load`;
    loadProducts(editor, {
        ...opts,
        idTableData: idTableData,
        idTableDataItem: idTableDataItem,
        idTableDataItemLoad: idTableDataItemLoad,
    });

    // Table Main
    const { url, method } = productData;
    editor.DomComponents.addType(id, {
        model: {
            defaults: {
                name: label,
                tagName: 'div',
                attributes: {
                    id: 'idx-my-products-all',
                    class: 'container',
                    "data-x-cls": "my-products-all",
                    "data-url": url,
                    "data-method": method,
                },
                dropable: false,
                components: [
                    { type: idToolbarHead },
                    { type: idTableData },
                    { type: idToolbarFoot },
                ],
                script: function () {
                    class toolbarLoad {
                        constructor(clsThis, resp) {
                            this.myProductAllEle = $(clsThis).closest('div[data-x-cls="my-products-all"]');
                            this.resp = resp;
                        }

                        static activeListenChangePage(myProductDataEle) {
                            let myProductAllEle = myProductDataEle.closest('div[data-x-cls="my-products-all"]');
                            $(myProductAllEle).find('.btn-page-controls').on('click', function () {
                                let pageRedirect = Number.parseInt($(this).attr('data-page'));
                                new productCall(myProductAllEle, myProductDataEle).callData({ page: pageRedirect });
                            });
                        }

                        loadPageCount() {
                            this.myProductAllEle.find('[data-x-cls="my-product-toolbar-query-count"]').text(
                                this.resp.page_count || ''
                            );
                        }


                        loadPagination() {
                            let pageCount = this.resp.page_count || 0;
                            let pageNext = this.resp.page_next || 0;
                            let pagePrevious = this.resp.page_previous || 0;
                            let pageSize = this.resp.page_size || 0;

                            // get Footer Ele & Hide all item
                            let footerEle = this.myProductAllEle.find('div[data-x-cls="my-product-footer"]');
                            footerEle.find('.btn-page-controls:not([data-x-cls="my-product-footer--previous-btn"]):not([data-x-cls="my-product-footer--next-btn"])').addClass('d-none btn-light').attr('data-page', 0);

                            // get button previous & next
                            let btnPrevous = footerEle.find('.btn-page-controls[data-x-cls="my-product-footer--previous-btn"]').prop('disabled', true);
                            let btnNext = footerEle.find('.btn-page-controls[data-x-cls="my-product-footer--next-btn"]').prop('disabled', true);

                            // handle page control
                            if (
                                Number.isInteger(pageCount)
                                && Number.isInteger(pageNext)
                                && Number.isInteger(pagePrevious)
                                && Number.isInteger(pageSize)
                            ) {
                                btnPrevous.removeClass('d-none');
                                btnNext.removeClass('d-none');
                                if (pageCount <= pageSize) {
                                    footerEle.find('[data-x-cls="my-product-footer--number-1"]').text(1).removeClass('d-none').prop('disabled', true);
                                } else {
                                    let pageCurrent = 0;

                                    if (pageNext == 0 && pagePrevious === 0) {
                                        pageCurrent = 0
                                    } else if (pageNext === 0) {
                                        pageCurrent = pagePrevious + 1;
                                    } else if (pagePrevious === 0) {
                                        pageCurrent = pageNext - 1;
                                    } else {
                                        pageCurrent = Number.parseInt((pagePrevious + pageNext) / 2);
                                    }


                                    let pageMax = Math.ceil(pageCount / pageSize);
                                    if (pageMax <= 9) {
                                        [...Array(pageMax).keys()].map(i => i + 1).map(
                                            (position) => {
                                                footerEle.find(
                                                    `[data-x-cls="my-product-footer--number-${position}"]`
                                                ).text(position).prop(
                                                    'disabled', position === pageCurrent ? true : false
                                                ).removeClass(
                                                    'd-none ' + (position === pageCurrent ? 'btn-light' : '')
                                                ).attr('data-page', position)
                                            }
                                        )
                                        btnPrevous.prop('disabled', pageCurrent === 1 ? true : false).attr('data-page', pageCurrent - 1);
                                        btnNext.prop('disabled', pageCurrent === pageMax ? true : false).attr('data-page', pageCurrent + 1);
                                    } else {
                                        if (pageCurrent <= 4) {
                                            // button control
                                            btnPrevous.attr('data-page', pageCurrent - 1).prop('disabled', pageCurrent === 1 ? true : false);
                                            btnNext.attr('data-page', pageCurrent + 1).prop('disabled', false);
                                            // all 
                                            footerEle.find(`[data-x-cls="my-product-footer--number-1"]`).text(1).attr('data-page', 1).prop('disabled', false).removeClass('d-none');
                                            footerEle.find(`[data-x-cls="my-product-footer--number-2"]`).text(2).attr('data-page', 2).prop('disabled', false).removeClass('d-none');
                                            footerEle.find(`[data-x-cls="my-product-footer--number-3"]`).text(3).attr('data-page', 3).prop('disabled', false).removeClass('d-none');
                                            footerEle.find(`[data-x-cls="my-product-footer--number-4"]`).text(4).attr('data-page', 4).prop('disabled', false).removeClass('d-none');
                                            footerEle.find(`[data-x-cls="my-product-footer--number-5"]`).text(5).attr('data-page', 5).prop('disabled', false).removeClass('d-none');
                                            footerEle.find(`[data-x-cls="my-product-footer--number-6"]`).text(6).attr('data-page', 6).prop('disabled', false).removeClass('d-none');
                                            footerEle.find(`[data-x-cls="my-product-footer--number-7"]`).text(7).attr('data-page', 7).prop('disabled', false).removeClass('d-none');
                                            footerEle.find(`[data-x-cls="my-product-footer--number-8"]`).text('...').prop('disabled', true).removeClass('d-none btn-light');
                                            footerEle.find(`[data-x-cls="my-product-footer--number-9"]`).text(pageMax).attr('data-page', pageMax).prop('disabled', false).removeClass('d-none');
                                            // current
                                            footerEle.find(`[data-x-cls="my-product-footer--number-${pageCurrent}"]`).prop('disabled', true).removeClass('d-none btn-light');
                                        } else if ((pageMax - pageCurrent) <= 4) {
                                            // button control
                                            btnPrevous.attr('data-page', pageCurrent - 1).prop('disabled', false);
                                            btnNext.attr('data-page', pageCurrent + 1).prop('disabled', pageCurrent === pageMax ? true : false);
                                            // all
                                            footerEle.find(`[data-x-cls="my-product-footer--number-1"]`).text(1).attr('data-page', 1).prop('disabled', false).removeClass('d-none');
                                            footerEle.find(`[data-x-cls="my-product-footer--number-2"]`).text('...').prop('disabled', true).removeClass('d-none btn-light');
                                            footerEle.find(`[data-x-cls="my-product-footer--number-3"]`).text(pageMax - 6).attr('data-page', pageMax - 6).prop('disabled', false).removeClass('d-none');
                                            footerEle.find(`[data-x-cls="my-product-footer--number-4"]`).text(pageMax - 5).attr('data-page', pageMax - 5).prop('disabled', false).removeClass('d-none');
                                            footerEle.find(`[data-x-cls="my-product-footer--number-5"]`).text(pageMax - 4).attr('data-page', pageMax - 4).prop('disabled', false).removeClass('d-none');
                                            footerEle.find(`[data-x-cls="my-product-footer--number-6"]`).text(pageMax - 3).attr('data-page', pageMax - 3).prop('disabled', false).removeClass('d-none');
                                            footerEle.find(`[data-x-cls="my-product-footer--number-7"]`).text(pageMax - 2).attr('data-page', pageMax - 2).prop('disabled', false).removeClass('d-none');
                                            footerEle.find(`[data-x-cls="my-product-footer--number-8"]`).text(pageMax - 1).attr('data-page', pageMax - 1).prop('disabled', false).removeClass('d-none');
                                            footerEle.find(`[data-x-cls="my-product-footer--number-9"]`).text(pageMax).attr('data-page', pageMax).prop('disabled', false).removeClass('d-none');
                                            // current
                                            footerEle.find(`[data-x-cls="my-product-footer--number-${9 - (pageMax - pageCurrent)}"]`).prop('disabled', true).removeClass('d-none btn-light');
                                        } else {
                                            // button control
                                            btnPrevous.attr('data-page', pageCurrent - 1).prop('disabled', false);
                                            btnNext.attr('data-page', pageCurrent + 1).prop('disabled', false);
                                            // left
                                            footerEle.find(`[data-x-cls="my-product-footer--number-4"]`).text(pageCurrent - 1).attr('data-page', pageCurrent - 1).prop('disabled', false).removeClass('d-none');
                                            footerEle.find(`[data-x-cls="my-product-footer--number-3"]`).text(pageCurrent - 2).attr('data-page', pageCurrent - 2).prop('disabled', false).removeClass('d-none');
                                            footerEle.find(`[data-x-cls="my-product-footer--number-2"]`).text('...').prop('disabled', true).removeClass('d-none btn-light');
                                            footerEle.find(`[data-x-cls="my-product-footer--number-1"]`).text(1).attr('data-page', 1).attr('data-page', 2).prop('disabled', false).removeClass('d-none');
                                            // right
                                            footerEle.find(`[data-x-cls="my-product-footer--number-6"]`).text(pageCurrent + 1).attr('data-page', pageCurrent + 1).prop('disabled', false).removeClass('d-none');
                                            footerEle.find(`[data-x-cls="my-product-footer--number-7"]`).text(pageCurrent + 2).attr('data-page', pageCurrent + 2).prop('disabled', false).removeClass('d-none');
                                            footerEle.find(`[data-x-cls="my-product-footer--number-8"]`).text('...').prop('disabled', true).removeClass('d-none btn-light');
                                            footerEle.find(`[data-x-cls="my-product-footer--number-9"]`).text(pageMax).attr('data-page', pageMax).prop('disabled', false).removeClass('d-none');
                                            // current
                                            footerEle.find(`[data-x-cls="my-product-footer--number-5"]`).text(pageCurrent).prop('disabled', true).removeClass('d-none btn-light');
                                        }
                                    }
                                }
                            }
                        }
                    }

                    class tableDataLoad {
                        constructor(ele, data) {
                            this.ele = ele;
                            this.data = data;
                        }

                        static convertThousand(value, currentCode = '') {
                            let arr = [];
                            if (value) {
                                let arrData = value.toString().split("").reverse();
                                arrData.map(
                                    (item, idx) => {
                                        if (idx !== 0 && idx % 3 === 0 && idx !== arrData.length - 1) {
                                            arr.push(".");
                                            arr.push(item);
                                        } else {
                                            arr.push(item);
                                        }
                                    }
                                )
                            }
                            return `${arr.reverse().join("")} ${currentCode.toUpperCase()}`;
                        }

                        load() {
                            this.loadLinkDetail();
                            this.loadSrcImage();
                            this.loadTitle();
                            this.loadPrice();
                            this.loadRemarks();
                            this.loadAvailabelAmout();
                        }

                        loadLinkDetail() {
                            let idx = this.data.id;
                            let aEle = $(this.ele).find('a[data-x-cls="my-product-item--link"]');
                            if (aEle.length > 0) {
                                if (idx) {
                                    let oldHref = aEle.attr('href');
                                    if (oldHref && oldHref !== '#'){
                                        aEle.attr(
                                            'href',
                                            oldHref.replaceAll("{id}", idx)
                                        )
                                    }
                                } else {
                                    aEle.attr('href', '#');
                                }
                            }
                        }

                        loadSrcImage() {
                            let imageData = this.data.avatar || 'data:image/svg+xml;base64,';
                            this.ele.find('img[data-x-cls="my-product-img--img"]').attr('src', imageData);
                        }

                        loadTitle() {
                            let titleFullText = this.data.title || '';

                            let titleEle = this.ele.find('[data-x-cls="title-line-text"]');
                            if (titleEle.length > 0) {
                                titleEle.attr('title', titleFullText);

                                let smallTitle = '';
                                if (titleFullText.length > 100) {
                                    smallTitle = `${titleFullText.slice(0, 100)}...`;
                                } else {
                                    smallTitle = `${titleFullText}${" ".repeat(100 - titleFullText)}`;
                                }
                                titleEle.attr('data-title-sm', smallTitle);
                                titleEle.text(smallTitle);
                            }
                        }

                        loadPrice() {
                            let price = this.data.sale_price || '';
                            this.ele.find('[data-x-cls="price-line-text"]').text(tableDataLoad.convertThousand(price));
                        }

                        loadRemarks() {
                            let remarkTxt = this.data.description || '';
                            let remarkEle = this.ele.find('[data-x-cls="remark-line-text"]');
                            remarkEle.attr('title', remarkTxt);

                            let smallRemark = '';
                            if (remarkTxt.length > 100) {
                                smallRemark = `${remarkTxt.slice(0, 100)}...`;
                            } else {
                                smallRemark = `${remarkTxt}${" ".repeat(100 - remarkTxt)}`;
                            }
                            remarkEle.attr('data-title-sm', smallRemark);
                            remarkEle.text(smallRemark);
                        }

                        loadAvailabelAmout() {
                            let amout = this.data.stock_amount || '';
                            if (amout) {
                                this.ele.find('[data-x-cls="my-product-data--amout-left"]').text(amout);
                            } else {
                                this.ele.find('[data-x-cls="my-product-data--amout-all"]').addClass('d-none');
                            }
                        }
                    }

                    class productCall {
                        constructor(clsThis, myProductDataEle) {
                            this.clsThis = clsThis;
                            this.myProductDataEle = myProductDataEle;
                        }

                        params() {
                            let paramData = {};

                            let searchTxt = $(this.clsThis).find('[data-x-cls="my-product-header--search"]').val();
                            if (searchTxt) paramData['search'] = searchTxt;
                            let sortTxt = $(this.clsThis).find('[data-x-cls="my-product-header--sort"]').val();
                            if (sortTxt === 'price-a-z') {
                                paramData['ordering'] = 'sale_price';
                            } else if (sortTxt === 'price-z-a') {
                                paramData['ordering'] = '-sale_price';
                            }
                            return paramData;
                        }

                        callData(extParam = {}) {
                            let clsThis = this;

                            // get pattern only one & remove exclude first
                            clsThis.myProductDataEle = $(clsThis.clsThis).find('div[data-x-cls="my-product-data"]');
                            clsThis.patternItemListEle = $(clsThis.clsThis).find('div[data-x-cls="my-product-item"][data-is-pattern="true"]').addClass('d-none');
                            clsThis.patternItemListEle.not(":eq(0)").remove();
                            clsThis.patternItemEle = null;
                            if (this.patternItemListEle.length === 1) {
                                this.patternItemEle = $(this.patternItemListEle[0]).clone();

                                // Old Item
                                let oldItem = $(clsThis.clsThis).find('div[data-x-cls="my-product-item"]:not([data-is-pattern="true"])');

                                if (oldItem.length === 0) {
                                    oldItem.remove();
                                    setTimeout(
                                        () => {
                                            clsThis.callDataApi(extParam);
                                        }, 50
                                    )
                                } else {
                                    clsThis.myProductDataEle.hide('fast', function () {
                                        $('html, body').animate({
                                            scrollTop: $('#idx-my-products-all').offset().top
                                        }, 50);
                                        oldItem.remove();
                                        setTimeout(
                                            () => {
                                                clsThis.callDataApi(extParam);
                                            }, 50
                                        )
                                    })
                                }
                            }
                        }

                        callDataApi(extParam = {}) {
                            let clsThis = this;

                            if (this.patternItemEle) {
                                let paramData = {
                                    pageSize: 12,
                                    page: 1,
                                    ...this.params(),
                                    ...extParam,
                                }
                                $.fn.callAjaxPublic({
                                    url: $(clsThis.clsThis).attr('data-url') + '?' + $.param(Object.keys(paramData).length > 0 ? paramData : {}),
                                    type: $(clsThis.clsThis).attr('data-method'),
                                }).then(
                                    (resp) => {
                                        let clsToolbar = new toolbarLoad(clsThis.clsThis, resp);
                                        clsToolbar.loadPageCount();
                                        clsToolbar.loadPagination();

                                        let data = resp?.['data'] || null;
                                        if (data && Array.isArray(data)) {
                                            data.map(
                                                (item, idx) => {
                                                    let childEle = this.patternItemEle.clone().removeClass('d-none').attr('data-id', item.id).attr('data-is-pattern', 'false');
                                                    new tableDataLoad(childEle, item).load();
                                                    $(myProductDataEle).append(childEle);

                                                    if (idx === data.length - 1) {
                                                        $(clsThis.myProductDataEle).show('fast')
                                                    }
                                                }
                                            )
                                        }
                                    },
                                    (errs) => {
                                        console.log('errs:', errs);
                                    }
                                )
                            }
                        }
                    }

                    let clsThis = this;
                    let myProductDataEle = $(this).find('div[data-x-cls="my-product-data"]');
                    if (myProductDataEle.length > 0) {
                        if ($(clsThis).attr('data-url') === '/test/products') {
                            console.log(
                                '%c Response My-Product API return format:',
                                'background: #222; color: red',
                                {
                                    "data": [
                                        {
                                            "id": "{id}",
                                            "title": "{name}",
                                            "available_amount": 40,
                                            "avatar": "{link}",
                                            "description": "{remark}",
                                            "sale_price": 1000,
                                        },
                                    ],
                                    "page_size": 100,
                                    "page_count": 1,
                                    "page_next": 0,
                                    "page_previous": 0
                                }
                            )

                            let productImgSrc = 'data:image/svg+xml;base64,';
                            let responseText = {
                                "data": [
                                    {
                                        "id": "3a82c590-cb82-4f7c-aba6-cfcb383af8e2",
                                        "title": "My in HP",
                                        "code": "PRD.0005",
                                        "stock_amount": 123,
                                        "avatar": "https://www.volusion.com/blog/content/images/2021/07/Product-Photos.jpg",
                                        "description": "My in mu",
                                        "sale_price": 15000000
                                    },
                                    {
                                        "id": "3a82c590-cb82-4f7c-aba6-cfcb383af8e2",
                                        "title": "My in HP",
                                        "code": "PRD.0005",
                                        "stock_amount": 666,
                                        "avatar": productImgSrc,
                                        "description": "My in mu",
                                        "sale_price": 15000000
                                    },
                                    {
                                        "id": "3a82c590-cb82-4f7c-aba6-cfcb383af8e2",
                                        "title": "My in HP",
                                        "code": "PRD.0005",
                                        "stock_amount": 834,
                                        "avatar": productImgSrc,
                                        "description": "My in mu",
                                        "sale_price": 15000000
                                    },
                                    {
                                        "id": "3a82c590-cb82-4f7c-aba6-cfcb383af8e2",
                                        "title": "My in HP",
                                        "code": "PRD.0005",
                                        "stock_amount": 1,
                                        "avatar": productImgSrc,
                                        "description": "My in mu",
                                        "sale_price": 15000000
                                    },
                                ],
                                "page_size": 10,
                                "page_count": 100,
                                "page_next": 3,
                                "page_previous": 1,
                            }
                            $.mockjax({
                                url: /\/test\/products(.)*/gm,
                                type: 'GET',
                                contentType: "application/json",
                                responseText: responseText,
                            });
                            $.mockjax({
                                url: '/test/products?pageSize=12&page=1',
                                type: 'GET',
                                contentType: "application/json",
                                responseText: responseText,
                            });
                            $.mockjax({
                                url: '/test/products?pageSize=12&page=2',
                                type: 'GET',
                                contentType: "application/json",
                                responseText: responseText,
                            });
                            $.mockjax({
                                url: '/test/products?pageSize=12&page=3',
                                type: 'GET',
                                contentType: "application/json",
                                responseText: responseText,
                            });
                        }
                        if (!$.fn.callAjaxPublic) {
                            $.fn.callAjaxPublic = function (config) {
                                return new Promise(
                                    (resolve, reject) => {
                                        let { url, method } = config;
                                        let ctx = {
                                            url: url,
                                            type: method, // dataType: 'json',
                                            contentType: "application/json",
                                            processData: true,
                                            data: 'GET',
                                            headers: {
                                            },
                                            success: function (rest, textStatus, jqXHR) {
                                                resolve(rest);
                                            },
                                            error: function (jqXHR, textStatus, errorThrown) {
                                                let resp_data = jqXHR.responseJSON;
                                                if (resp_data && typeof resp_data === 'object') {
                                                    reject(resp_data);
                                                } else if (jqXHR.status === 204) reject({ 'status': 204 });
                                            },
                                        }
                                        return $.ajax(ctx);
                                    }
                                )
                            }
                        }

                        toolbarLoad.activeListenChangePage(myProductDataEle);
                        new productCall(clsThis, myProductDataEle).callData();

                        $(clsThis).find('[data-x-cls="my-product-header--search"],[data-x-cls="my-product-header--sort"]').on('change', function () {
                            new productCall(clsThis, myProductDataEle).callData();
                        });
                    }
                },
            },
            init() {
                if (this.components().models[1].components().length === 0) {
                    this.components().models[1].components().add({ type: idTableDataItem });
                }
            },
        },
    });
}