import { dataConst } from "../../const";

export default (editor, opts = {}) => {
    const { idColInfo, labelColInfo, internalOpts } = opts;

    // row label
    const [idRowLabelAds, labelRowLabelAds] = [`${idColInfo}-row-label`, `${labelColInfo} Row Label`];
    editor.DomComponents.addType(idRowLabelAds, {
        model: {
            defaults: {
                ...internalOpts,
                name: labelRowLabelAds,
                tagName: 'div',
                attributes: {
                    class: "row p-0 mb-1",
                    "data-x-cls": "my-product-detail--banner",
                },
                components: [
                    {
                        tagName: 'h6',
                        attributes: {
                            class: 'bg-danger text-white rounded px-2 py-1'
                        },
                        components: `MALL`
                    }
                ],
            }
        }
    })

    // row title
    const [idRowTitle, labelRowTitle] = [`${idColInfo}-row-title`, `${labelColInfo} Row Title`];
    editor.DomComponents.addType(idRowTitle, {
        model: {
            defaults: {
                ...internalOpts,
                name: labelRowTitle,
                tagName: 'div',
                attributes: {
                    class: "row p-0",
                },
                components: [
                    {
                        tagName: 'h5',
                        attributes: {
                            "data-x-cls": "my-product-detail--title",
                        },
                        components: `TITLE`,
                    }
                ]
            }
        }
    })

    // row price
    const [idRowPrice, labelRowPrice] = [`${idColInfo}-row-price`, `${labelColInfo} Row Price`];
    editor.DomComponents.addType(idRowPrice, {
        model: {
            defaults: {
                ...internalOpts,
                name: labelRowPrice,
                tagName: 'div',
                attributes: {
                    class: "row p-0",
                },
                components: [
                    {
                        tagName: 'p',
                        attributes: {
                            class: 'text-warning h4 mb-3',
                            'data-x-cls': 'my-product-detail--price',
                        },
                        components: `0 `,
                    },
                    {
                        tagName: 'div',
                        attributes: {
                            class: 'w-100 mb-3',
                            "data-x-cls": "my-product-detail--price--off",
                        },
                        components: [
                            {
                                tagName: 'span',
                                attributes: {
                                    class: 'ml-1 text-muted',
                                },
                                components: '<del>0 </del>',
                            },
                            {
                                tagName: 'small',
                                attributes: {
                                    class: 'ml-1 badge badge-light',
                                },
                                components: `0%`,
                            },
                        ]
                    },
                    {
                        tagName: 'div',
                        attributes: {
                            class: 'mb-3',
                            "data-x-cls": "my-product-detail--favour",
                        },
                        components: [
                            {
                                tagName: 'p',
                                components: `${editor.I18n.t('grapesjs-plugin-my-products.favourPrice')}:`,
                            },
                            {
                                tagName: 'p',
                                attributes: {
                                    class: 'bg-warning text-white text-wrap p-1 m-1 border rounded',
                                },
                                components: `Favour 1`
                            },
                            {
                                tagName: 'p',
                                attributes: {
                                    class: 'bg-warning text-white text-wrap p-1 m-1 border rounded',
                                },
                                components: `Favour 2`
                            },
                        ]
                    },
                ]
            }
        }
    })

    // row variant
    const [idRowVariant, labelRowVadiant] = [`${idColInfo}-row-variant`, `${labelColInfo} Row Variant`];
    editor.DomComponents.addType(idRowVariant, {
        model: {
            defaults: {
                ...internalOpts,
                name: labelRowVadiant,
                components: [
                    {
                        tagName: 'div',
                        attributes: {
                            class: 'chips-group d-inline-block mr-1',
                        },
                        components: [
                            {
                                tagName: 'input',
                                attributes: {
                                    type: "radio",
                                    id: 'variant1',
                                    name: "select-variant",
                                },
                            },
                        ],
                        script: function(){
                            function makeid(length) {
                                let result = '';
                                const characters = 'ABCDEFGHIJKLMNOPQRSTUVWXYZabcdefghijklmnopqrstuvwxyz0123456789';
                                const charactersLength = characters.length;
                                let counter = 0;
                                while (counter < length) {
                                  result += characters.charAt(Math.floor(Math.random() * charactersLength));
                                  counter += 1;
                                }
                                return result;
                            }
                            

                            let idx = makeid(18);

                            $(this).find('input').attr('id', idx);
                            $(this).find('label').attr('for', idx);
                        }
                    }
                ],
                styles: `
                .chips-group {
                    display: inline-block;
                }
                .chips-group input {
                    display: none;
                }
                .chips-group input+label {
                    -webkit-transition: all 100ms ease;
                    transition: all 100ms ease;
                    cursor: pointer;
                    border-radius: 5px;
                    border: 2px solid #f1f1f1;
                    padding: 0.5rem 0.5rem;
                    display: inline-block;
                    -moz-user-select: -moz-none;
                    -webkit-user-select: none;
                    -ms-user-select: none;
                    user-select: none;
                }
                .chips-group input+label>svg {
                    display: none;
                }
                .chips-group input:checked+label>svg {
                    display: block;
                }
                .chips-group input:checked+label {
                    -webkit-transition: all 100ms ease;
                    transition: all 100ms ease;
                    border-color: #0035a7;
                }
                .chips-group label {
                    position: relative;
                }
                .chips-group label img {
                    width: 25px;
                    height: 25px;
                }
                .chips-group label svg {
                    position: absolute;
                    bottom: 0;
                    right: 0;
                    width: 15px;
                    height: 15px;
                }
                `,
            }
        }
    })

    const [idRowVariantGroup, labelRowVariantGroup] = [`${idColInfo}-row-variant-group`, `${labelColInfo} Row Variant Group`];
    editor.DomComponents.addType(idRowVariantGroup, {
        model: {
            defaults: {
                ...internalOpts,
                name: labelRowVariantGroup,
                tagName: 'div',
                attributes: {
                    class: 'mt-1',
                    "data-x-cls": "my-product-detail--variant",
                },
                components: [
                    {
                        tagName: 'p',
                        components: `${editor.I18n.t("grapesjs-plugin-my-products.variantProd")}:`,
                    },
                    {
                        tagName: 'div',
                        attributes: {
                            class: 'd-flex'
                        },
                        components: [
                            {type: idRowVariant},
                            {type: idRowVariant},
                            {type: idRowVariant},
                            {type: idRowVariant},
                        ]
                    }
                ]
            }
        }
    })

    // row quantity
    const [idRowQuantity, labelRowQuantity] = [`${idColInfo}-row-quantity`, `${labelColInfo} Row Quantity`];
    editor.DomComponents.addType(idRowQuantity, {
        model: {
            defaults: {
                ...internalOpts,
                name: labelRowQuantity,
                tagName: 'div', 
                attributes: {
                    class: 'mt-1',
                },
                components: [
                    {
                        tagName: 'label',
                        attributes: {
                            for: 'input-quantity',
                        },
                        components: `${editor.I18n.t('grapesjs-plugin-my-products.quantityBuy')}:`,
                    },
                    {
                        tagName: 'input',
                        attributes: {
                            id: 'input-quantity',
                            class: 'form-control',
                            placeholder: '',
                            "aria-label": '',
                            "aria-describedby": '',
                            type: 'number',
                            min: 0,
                            value: 1,
                        }
                    },
                ]
            }
        }
    })

    // row control
    const [idRowBtnGroup, labelRowBtnGroup] = [`${idColInfo}-row-btn`, `${labelColInfo} Row Button`];
    const [idRowBtnAddCart, labelRowBtnAddCard] = [`${idColInfo}-row-add-card`, `${labelColInfo} Row Add Card`];
    const [idRowBtnBuyNow, labelRowBtnBuyNow] = [`${idColInfo}-row-buy-now`, `${labelColInfo} Row Buy Now`];

    editor.DomComponents.addType(idRowBtnBuyNow, {
        model: {
            defaults: {
                ...internalOpts,
                name: labelRowBtnBuyNow,
                attributes: {
                    type: 'button',
                    class: 'btn btn-warning mr-1 text-white',
                },
                tagName: 'button',
                components: `${editor.I18n.t("grapesjs-plugin-my-products.buyNow")}`,
            }
        }
    })

    editor.DomComponents.addType(idRowBtnAddCart, {
        model: {
            defaults: {
                ...internalOpts,
                name: labelRowBtnAddCard,
                attributes: {
                    type: 'button',
                    class: 'btn btn-primary mr-1',
                },
                tagName: 'button',
                components: `${editor.I18n.t("grapesjs-plugin-my-products.addToCart")}`,
            }
        }
    })

    editor.DomComponents.addType(idRowBtnGroup, {
        model: {
            defaults: {
                ...internalOpts,
                name: labelRowBtnGroup,
                tagName: 'div',
                attributes: {
                    class: 'mt-3',
                },
                components: [
                    {type: idRowBtnBuyNow},
                    {type: idRowBtnAddCart},
                ]
            }
        }
    })


    // main
    editor.DomComponents.addType(idColInfo, {
        model: {
            defaults: {
                ...internalOpts,
                name: labelColInfo,
                attributes: {
                    class: `col-12 col-lg-5 p-3`,
                },
                components: [
                    { type: idRowLabelAds },
                    { type: idRowTitle },
                    { tagName: 'hr' },
                    { type: idRowPrice },
                    { tagName: 'hr' },
                    { type: idRowVariantGroup },
                    { type: idRowQuantity },
                    { type: idRowBtnGroup},
                ]
            }
        }
    })
}