import { dataConst } from "../../const";

export default (editor, opts = {}) => {
    const {
        prefixClass, extClass,
        internalOpts,
        idSlider, labelSlide,
        idSliderItem, labelSliderItem,
    } = opts;

    editor.DomComponents.addType(idSliderItem, {
        model: {
            defaults: {
                ...internalOpts,
                name: labelSliderItem,
                tagName: 'div',
                attributes: {
                    class: `${prefixClass}-item`,
                    "data-x-cls": `${prefixClass}-item`,
                },
                components: {
                    type: 'image',
                    tagName: 'img',
                    attributes: {
                        src: dataConst.sliderImg3x2,
                    },
                }
            }
        }
    })

    editor.DomComponents.addType(idSlider, {
        model: {
            defaults: {
                ...internalOpts,
                name: labelSlide,
                tagName: 'section',
                attributes: {
                    class: `${prefixClass} ${extClass}`,
                    "data-x-cls": `${prefixClass}`,
                },
                components: [],
            }
        }
    })
}