export default (editor, opts = {}) => {
    const { idColDelivery, labelColDelivery, internalOpts } = opts;


    // Address
    const [idColAddr, labelColAddr] = [`${idColDelivery}-col-addr`, `${labelColDelivery} Col Address`];
    editor.DomComponents.addType(idColAddr, {
        model: {
            defaults: {
                ...internalOpts,
                name: labelColAddr,
                tagName: 'div',
                attributes: {},
                components: [
                    {
                        tagName: 'p',
                        attributes: {
                            class: "text-muted",
                        },
                        components: editor.I18n.t("grapesjs-plugin-my-products.addrDelivery"),
                    },
                    {
                        tagName: 'div',
                        attributes: {
                            class: "row"
                        },
                        components: [
                            {
                                tagName: 'div',
                                attributes: {
                                    class: "col-8",
                                },
                                components: 'H Ch Minh, Qun 1, Phng Phm Ng Lo',
                            },
                            {
                                tagName: 'div',
                                attributes: {
                                    class: "col-4",
                                },
                                components: {
                                    tagName: 'button',
                                    attributes: {
                                        class: 'btn btn-light',
                                    },
                                    components: editor.I18n.t("grapesjs-plugin-my-products.btnChange"),
                                },
                            },
                        ]
                    }
                ],
            }
        }
    })

    // Ship code
    const [idColShip, labelColShip] = [`${idColDelivery}-col-ship`, `${labelColDelivery} Col Ship`];
    editor.DomComponents.addType(idColShip, {
        model: {
            defaults: {
                ...internalOpts,
                name: labelColShip,
                tagName: 'div',
                components: [
                    {
                        tagName: 'p',
                        attributes: {
                            class: "text-muted",
                        },
                        components: editor.I18n.t("grapesjs-plugin-my-products.shipCode"),
                    },
                    {
                        tagName: 'div',
                        attributes: {
                            class: "row",
                        },
                        components: [
                            {
                                tagName: 'div',
                                attributes: {
                                    class: "col-8",
                                },
                                components: `Giao hng tiu chun`,
                            },
                            {
                                tagName: 'div',
                                attributes: {
                                    class: "col-4",
                                },
                                components: `16.000 `
                            },
                            {
                                tagName: 'div',
                                attributes: {
                                    class: "col-12 mt-2",
                                },
                                components: `Thanh ton khi nhn hng. Khng c ng kim.`
                            },
                        ],
                    }
                ]
            }
        }
    })

    // warranty
    const [idColWarranty, labelColWarranty] = [`${idColDelivery}-col-warranty`, `${labelColDelivery} Col Warranty`];
    editor.DomComponents.addType(idColWarranty, {
        model: {
            defaults: {
                ...internalOpts,
                name: labelColWarranty,
                attributes: {
                    "data-x-cls": "my-product-detail--warranty"
                },
                components: [
                    {
                        tagName: 'p',
                        attributes: {
                            class: "text-muted",
                        },
                        components: editor.I18n.t("grapesjs-plugin-my-products.exchangeAndWarranty"),
                    },
                    {
                        tagName: 'p',
                        components: [
                            {
                                tagName: 'i',
                                attributes: {
                                    class: "fa-solid fa-hourglass-half text-success mr-2",
                                }
                            },
                            {
                                tagName: 'span',
                                components: editor.I18n.t("grapesjs-plugin-my-products.returnable"),
                            }
                        ],
                    },
                    {
                        tagName: 'p',
                        components: [
                            {
                                tagName: 'i',
                                attributes: {
                                    class: "fa-solid fa-arrow-rotate-left text-success mr-2",
                                }
                            },
                            {
                                tagName: 'span',
                                components: `7`,
                            },
                            {
                                tagName: 'span',
                                components: editor.I18n.t("grapesjs-plugin-my-products.dayReturned"),
                            }
                        ],
                    },
                    {
                        tagName: 'p',
                        components: [
                            {
                                tagName: 'i',
                                attributes: {
                                    class: "fa-solid fa-shield text-dark mr-2",
                                }
                            },
                            {
                                tagName: 'span',
                                components: editor.I18n.t("grapesjs-plugin-my-products.noApplyWarranty"),
                            }
                        ],
                    },
                ]
            }
        }
    })

    // main
    editor.DomComponents.addType(idColDelivery, {
        model: {
            defaults: {
                ...internalOpts,
                name: labelColDelivery,
                attributes: {
                    class: `col-12 col-lg-3 p-3 bg-light`,
                },
                components: [
                    { type: idColAddr },
                    { tagName: 'hr' },
                    { type: idColShip },
                    { tagName: 'hr' },
                    { type: idColWarranty },
                ]
            }
        }
    })
}